.footer__menu__column__container {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 40px;
}
@media(max-width:991.98px){
    .footer__menu__column__container  {
        gap: 20px;
    }
}
.footer__menu__column__title {
    color: #FFF;
    font-family: "Source Sans Pro";
    font-size: 22px;
    font-style: normal;
    font-weight: 700;
    line-height: 120%;
}

.footer__menu__column__items {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 16px;
}
@media(max-width:991.98px){
    .footer__menu__column__items {
        flex-direction: row;
    }
}
.footer__menu__column__item {
    color: var(--Theme-Gray-100, #F5F5F5);
    font-family: "Source Sans Pro";
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 100%;
}