* {
  font-family: 'Source Sans Pro';
  margin: 0;
  box-sizing: border-box;
}

::placeholder {
  color: var(--theme-gray-500, #9E9E9E);
}

body {
  background-color: #ffff;
}


body, html, #root, .container {
  min-width: 320px;
  width: 100%; 
  /* height: 100%;  */
}

.container {
  display: flex;
  flex-direction: column;
}

/* .section__container {
  max-width: 1481px;
} */

.section__title {
  text-align: center;
  font-family: Source Sans Pro;
  font-size: 43px;
  font-style: normal;
  font-weight: 700;
  line-height: 112%;
}

.section__wrapper {
  min-width: 1480px;
  /* max-width: 1800px; */
  /* margin: 0 auto; */
  /* overflow: hidden; */
}

.section__list {
  max-width: 1481px;
}

@media (max-width: 1480px) {
  .section__wrapper {
      min-width: 730px;
  }
}

@media (max-width: 1100px) {
  .section__list {
      max-width: 730px;
  }
}

@media (max-width: 735px) {
  .section__wrapper {
    min-width: auto;
    max-width: auto;
  }
}

@media (max-width: 380px) {
  .section__title {
    font-size: 35px;
  }

}

